import React from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import AuthService from '../AuthService';
import { history } from '../RedirectService';

import {
    Col, Form,
    Button, InputGroup
} from 'react-bootstrap';

const SERVER_URL = "http://localhost:4000";

function SignInForm() {

    const [validated, setValidated] = React.useState(false);
    const [username, setUsername] = React.useState("");
    const [password, setPassword] = React.useState("");
    const Auth = new AuthService();

    const handleSubmit = event => {
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            console.log("checkValidity = false got hit"); // droebit
            event.preventDefault();
            event.stopPropagation();
            setValidated(true);
        } else {
            event.preventDefault();
                const userObj = {
                    username: username,
                    password: password
                };
        
                axios.post(SERVER_URL + '/api/users/login', userObj)
                .then((res) => {
                     if (res.status === 200) {
                        console.log(res);
                        console.log("axios got hit"); // droebit
                        localStorage.setItem("id_token", res.data.token);
                        window.location.href = '/'; // droebit
                     } else {
                        console.log('else'); // droebit
                     }
                })
                .catch((error) => console.log(error));
                setValidated(true);
        }
        console.log("setvalidated got hit"); // droebit
        // setValidated(true);
    }


    // function usernameChangeHandler() {

    // };

    // function passwordChangeHandler() {

    // }

    const isFormEmpty = () => {
        return !username.length > 0 || !password.length > 0; // droebit
    }
    React.useEffect(() => {
        if (Auth.loggedIn())
            window.location.href = '/#'; // droebit
     }, [Auth]);
    return (
        <Form noValidate validated={validated} style={{maxWidth:1700}} onSubmit={handleSubmit}>
            <Form.Row>
                <Form.Group as={Col} md="3" controlId="validationCustomUsername">
                    <Form.Label style={{marginLeft:20}}>Username</Form.Label>
                    <InputGroup style={{marginLeft:20}}>
                        <InputGroup.Prepend>
                            <InputGroup.Text id="inputGroupPrepend">@</InputGroup.Text>
                        </InputGroup.Prepend>
                        <Form.Control
                            type="text"
                            
                            placeholder="Username"
                            onChange={e => setUsername(e.target.value)}
                            aria-describedby="inputGroupPrepend"
                            required
                        />
                        <Form.Control.Feedback type="invalid">
                            Please type in your username.
                        </Form.Control.Feedback>
                    </InputGroup>
                </Form.Group>
            </Form.Row>
            <Form.Row>
                <Form.Group style={{marginLeft:20}} as={Col} md="3" controlId="validationCustomPassword">
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                        required
                        type="password"
                        onChange={e => setPassword(e.target.value)}
                        placeholder="Password"
                    />
                    <Form.Control.Feedback type="invalid">
                        Please type in your password.
                    </Form.Control.Feedback>
                </Form.Group>
            </Form.Row>
            {/* <Form.Group>
                <div className="form-group form-check">
                    <input required type="checkbox" className="form-check-input" id="term_check" />
                    <label className="form-check-label" htmlFor="term_check">Agree to terms and conditions</label>
                </div>
            </Form.Group> */}
            <Button style={{marginLeft:20}} type="submit" /*disabled={isFormEmpty()}*/>Sign in</Button>
            <Button href="#registracia" style={{marginLeft:7}}>Don't have an account?</Button>
        </Form>
    );
}

export default SignInForm;
