import React, { Component, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import {
     Col, Form,
     Button
} from 'react-bootstrap';

const DB_URL = "http://localhost:4000";

export default class SignUp extends Component {
     constructor(props) {
          super(props);
          this.state = { users: [] };

          this.onChangeUsername = this.onChangeUsername.bind(this);
          this.onChangePassword = this.onChangePassword.bind(this);
          this.onChangeGender = this.onChangeGender.bind(this);
          this.onSubmit = this.onSubmit.bind(this);

          this.state = {
               username: '',
               password: '',
               gender: ''
          }
     }

     onChangeUsername(e) {
          this.setState({
               username: e.target.value
          });
     }

     onChangePassword(e) {
          this.setState({
               password: e.target.value
          });
     }

     onChangeGender(e) {
          this.setState({
               gender: e.target.value
          });
     }

     onSubmit(e) {
          e.preventDefault();

          console.log(`დარეგისტრირდი:`);
          console.log(`იუზერი: ${this.state.username}`);
          console.log(`პაროლი: ${this.state.password}`);
          console.log(`სქესი: ${this.state.gender}`);

          const newUser = {
               username: this.state.username,
               password: this.state.password,
               gender: this.state.gender
          };

          function appendMsg(condition) {
               var msg_div = document.createElement("div");
               msg_div.className = `alert alert-${condition} center-block navbar-form text-center`;
               if (condition === "success") msg_div.innerHTML = "წარმატებით დარეგისტრირდი."; else msg_div.innerHTML = "რეგისტრაციისას მოხდა შეცდომა.";
               document.getElementById('signupContainer').insertBefore(msg_div, document.getElementById('signupContainer').firstChild); // droebit
          }

          axios.post(DB_URL + '/api/users/register', newUser)
               .then((res) => {
                    if (res.status === 200) {
                         appendMsg("success");

                         console.log(res);
                    } else {
                         appendMsg("danger");
                    }
               })
               .catch((error) => {
                    console.log(error);
                    appendMsg("danger");
               });


          this.setState({
               username: '',
               password: '',
               gender: ''
          })


     }
     render() {
          return (
               <div id="wrapper">
                    <div id='signupContainer'>
                         <center>
                              <div style={{ width: 600, textAlign: 'left', padding: 10, marginTop: 10, color: 'white' }} className="block-example border border-primary rounded mb-0">
                                   <Form onSubmit={this.onSubmit}>
                                        <Form.Group as={Form.Row} controlId="formHorizontalUsername">
                                             <Form.Label column sm={2}>
                                                  იუზერი
                                        </Form.Label>
                                             <Col sm={10}>
                                                  <Form.Control type="text" required value={this.state.username} onChange={this.onChangeUsername} placeholder="იუზერი" />
                                             </Col>
                                        </Form.Group>

                                        <Form.Group as={Form.Row} controlId="formHorizontalPassword">
                                             <Form.Label column sm={2}>
                                                  პაროლი
                                        </Form.Label>
                                             <Col sm={10}>
                                                  <Form.Control type="password" required value={this.state.password} onChange={this.onChangePassword} placeholder="შეიყვანე პაროლი" />
                                             </Col>
                                        </Form.Group>
                                        <fieldset>
                                             <Form.Group as={Form.Row}>
                                                  <Form.Label as="legend" column sm={2}>
                                                       სქესი:
                                             </Form.Label>
                                                  <Col sm={10}>
                                                       <Form.Check
                                                            type="radio"
                                                            label="ბიჭი"
                                                            name="genderOptions"
                                                            id="genderMale"
                                                            onChange={this.onChangeGender}
                                                            value="ბიჭი"
                                                            required
                                                       />
                                                       <Form.Check
                                                            type="radio"
                                                            label="გოგო"
                                                            name="genderOptions"
                                                            id="genderFemale"
                                                            onChange={this.onChangeGender}
                                                            value="გოგო"
                                                            required
                                                       />
                                                       <Form.Check
                                                            type="radio"
                                                            label="არ მიფიქრია"
                                                            name="genderOptions"
                                                            id="notSure"
                                                            onChange={this.onChangeGender}
                                                            value="არ მიფიქრია"
                                                            required
                                                       />
                                                  </Col>
                                             </Form.Group>
                                        </fieldset>
                                        <Form.Group as={Form.Row} controlId="formHorizontalCheck">
                                             <Col sm={{ span: 10, offset: 2 }}>
                                                  <Form.Check label="მაგარი საიტია" />
                                             </Col>
                                        </Form.Group>

                                        <Form.Group as={Form.Row}>
                                             <Col sm={{ span: 10, offset: 2 }}>
                                                  <Button type="submit" value="Register">დარეგისტრირება</Button>
                                                  <Link to="/"><Button style={{ marginLeft: 20 }}>დაბრუნება</Button></Link>
                                             </Col>
                                        </Form.Group>
                                   </Form>
                              </div>
                         </center>
                    </div>
               </div>
          )
     }
}